.checkout-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
  }
  
  .quick-login {
    width: 100%;
    
    margin-bottom: 20px;
  }
  
  .quick-login p {
    margin: 0;
  }
  
  .quick-login a {
    color: #007bff;
    text-decoration: none;
  }
  
  .quick-login a:hover {
    text-decoration: underline;
  }
  
  .checkout {
    display: flex;
    justify-content: space-between;
  }
  
  .customer-info,
  .product-info,
  .payment-info {
    flex-basis: 30%;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .customer-info h3,
  .product-info h3,
  .payment-info h3 {
    margin-top: 0;
  }
  
  .customer-info input,
  .customer-info select {
    display: block;
    width: 100%;
    margin-bottom: 10px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
  }
  
  .product-info .product-item {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
  }
  
  .product-info .product-item img {
    width: 80px;
    height: 80px;
    object-fit: cover;
    margin-right: 20px;
  }
  
  .product-info .quantity-control {
    display: flex;
    align-items: center;
    margin-top: 10px;
  }
  
  .product-info .quantity-control button {
    padding: 5px 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background-color: #f5f5f5;
    cursor: pointer;
  }
  
  .product-info .quantity-control span {
    margin: 0 10px;
  }
  
  .product-info .quantity-control button i {
    color: #ff6b6b;
  }
  
  .payment-info .totals {
    margin-bottom: 20px;
  }
  
  .payment-info .total-row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
  }
  
  .payment-info .total {
    font-weight: bold;
    border-top: 1px solid #ddd;
    padding-top: 10px;
  }
  
  .payment-info .checkout-btn {
    display: block;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 4px;
    background-color: #ff6b6b;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
  }
  
  .payment-info .checkout-btn:disabled {
    background-color: #ddd;
    cursor: not-allowed;
  }
  
  .payment-info p {
    color: #ff6b6b;
    text-align: center;
  }
  
  .payment-info h4 {
    margin-top: 20px;
  }
  
  .payment-info .payment-methods {
    margin-bottom: 20px;
  }
  
  .payment-info .method {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
  }
  
  .payment-info .method input[type="radio"] {
    margin-right: 10px;
  }
  
  .payment-info .method label {
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  .payment-info .method i {
    margin-right: 10px;
  }
  
  .payment-info textarea {
    display: block;
    width: 100%;
    height: 80px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    margin-bottom: 10px;
  }
  
  .payment-info .order-btn {
    display: block;
    width: 100%;
    padding: 10px;
    border: none;
    border-radius: 4px;
    background-color: #ff6b6b;
    color: #fff;
    font-weight: bold;
    cursor: pointer;
  }
  