.login-button {
  background-color: #0f7fc1; /* Mu nn ca nt */
  color: whitesmoke; /* Mu ch ca nt */
  border: 1px solid #ddd; /* Vin ca nt */
  border-radius: 20px; /* Bo gc ca nt */
  padding: 10px 20px; /* Kch thc padding ca nt */
  font-size: 16px; /* Kch thc font ch ca nt */
  cursor: pointer;
  display: flex;
  align-items: center;
}

.login-button:hover {
  background-color: rgb(201, 166, 101); /* Mu nn ca nt khi hover */
}


.google-signup-button {
  display: inline-block;
  color: whitesmoke;
  text-decoration: none;
  padding: 0.5rem 1rem;
  border-radius: 0.25rem;
  border: 1px solid black;
  background-color: rgb(194, 57, 11);
  transition: background-color 0.3s ease;
}

.google-signup-button:hover {
  background-color: rgb(201, 166, 101);
}

.google-icon {
  margin-right: 10px;
}

.button-text {
  vertical-align: middle;
}
